<template>
    <div id="app">
        <pre style="text-align: left">config: {{ config }}</pre>
        <button @click="scan">扫一扫</button>
        <div>result: {{ result }}</div>
    </div>
</template>

<script>
import wx from 'weixin-js-sdk';
export default {
    name: 'App',
    components: {},
    data() {
        return {
            result: '',
            config: {
                debug: true,
                appId: 'wxd8c2f7f1955ab6ba',
                timestamp: 1634865805,
                nonceStr: '0a9f3543034144578a14349de1451741',
                signature: '335809563dad630c7397c79bb0df43c191061551',
                jsApiList: ['checkJsApi', 'scanQRCode'],
            },
        };
    },
    created() {
        wx.config({
            ...this.config,
        });
        wx.error(function (res) {
            alert('出错了：' + res.errMsg); //这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
        });
    },
    methods: {
        scan() {
            wx.scanQRCode({
                needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                scanType: ['qrCode'], // 可以指定扫二维码还是一维码，默认二者都有
                success: function (res) {
                    // self.$toast('weixin scanQRCode success', 'success');
                    this.result = res.resultStr;
                },
            });
        },
    },
};
</script>

<style>
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}
</style>
